body {
  background-color: #f1f1f1;
  font-family: "ProximNovExtrCondenseRegula", 'Arial Narrow', Arial, Helvetica, sans-serif;
  font-size-adjust: 0.488;
  font-weight: normal;
  font-size: 18px;
  font-style: normal;
}
[ng\:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x-ng-cloak {
  display: none !important;
}
.badge {
  background-color: #D0021B;
  display: inline;
  text-transform: uppercase;
}
.badge.badge-green {
  background-color: #8ED62B;
  color: #000;
}
.navbar {
  background-color: #DFDEDF;
  border-radius: 0;
  margin-bottom: 0;
}
.navbar.navbar-top {
  background-color: #07256C;
  min-height: 30px;
}
.navbar.navbar-top .nav.navbar-nav > li {
  color: #fff;
  line-height: 30px;
}
.navbar.navbar-top .nav.navbar-nav > li > a {
  background-color: #07256C;
  color: #fff;
  padding: 5px;
  text-transform: uppercase;
}
.navbar.navbar-top .nav.navbar-nav > li > a.separator:after {
  content: '/';
  margin-left: 10px;
}
.navbar.navbar-top .nav.navbar-nav > li > a.active {
  text-decoration: underline;
}
.navbar.navbar-top .nav.navbar-nav > li:last-child > a.separator:after {
  content: '';
}
.navbar.navbar-default .navbar-brand {
  background-color: #fff;
}
.navbar.navbar-default .navbar-brand > img {
  max-height: 20px;
}
.navbar.navbar-default .nav.navbar-nav > li {
  background-color: #000;
  border-right: 1px solid #fff;
}
.navbar.navbar-default .nav.navbar-nav > li.active > a {
  background-color: #07256C;
  color: #fff;
}
.navbar.navbar-default .nav.navbar-nav > li > a {
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
}
.navbar.navbar-default .nav.navbar-nav.navbar-inverse > li {
  border: none;
}
.navbar.navbar-default .nav.navbar-nav.navbar-inverse > li > a {
  background-color: #DFDEDF;
  color: #000;
  text-transform: uppercase;
}
.navbar.navbar-default .navbar-form {
  margin: 0;
  padding: 0;
}
.navbar.navbar-default .navbar-form .input-group-addon {
  background-color: #000;
  border: none;
  border-radius: 0;
  color: #fff;
  line-height: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.navbar.navbar-default .navbar-form input.form-control {
  border: none;
  border-radius: 0;
  height: inherit;
  line-height: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.navbar.navbar-default .navbar-form.navbar-right {
  margin-left: -30px;
  margin-right: -30px;
}
@media (max-width: 767px) {
  .navbar.navbar-default .navbar-form.navbar-right {
    margin-left: -15px;
    margin-right: -15px;
  }
}
.navbar.navbar-default .badge {
  vertical-align: inherit;
}
.navbar.navbar-default .navbar-toggle {
  background-color: #000;
  border-radius: 0;
  height: 50px;
  margin: 0;
  padding: 15px;
}
.navbar.navbar-default .navbar-toggle .icon-bar {
  background-color: #fff;
}
footer {
  background-color: #07256C;
  color: #fff;
}
footer a {
  color: #fff;
}
footer h3 {
  text-transform: uppercase;
}
footer .fa-inverse {
  color: #07256C;
}
.container .jumbotron,
.container-fluid .jumbotron {
  background-color: #111;
  background-position: center;
  background-size: cover;
  border-radius: 0;
  margin: 0 -15px 20px -15px;
  padding: 0;
  position: relative;
}
.container .jumbotron .img-background,
.container-fluid .jumbotron .img-background {
  filter: blur(10px);
  height: 100%;
  opacity: 0.2;
  position: absolute;
  width: 100%;
}
.container .jumbotron h1,
.container-fluid .jumbotron h1 {
  color: #fff;
  font-size: 40px;
  font-weight: bold;
}
.container .jumbotron p,
.container-fluid .jumbotron p {
  color: #fff;
}
.container .jumbotron p.author,
.container-fluid .jumbotron p.author {
  font-weight: bold;
  padding-bottom: 12px;
  padding-top: 12px;
}
.container .jumbotron .jumbotron-inner,
.container-fluid .jumbotron .jumbotron-inner {
  padding: 20px;
}
.container .jumbotron .jumbotron-inner img,
.container-fluid .jumbotron .jumbotron-inner img {
  padding-bottom: 25px;
  padding-top: 25px;
}
.no-padding {
  padding: 0;
}
.text-green {
  color: #8ED62B !important;
}
.text-red {
  color: #E4453B !important;
}
.text-white {
  color: #fff!important;
}
.text-light-gray {
  color: #B4B4B4!important;
}
.border-white {
  border-color: #fff!important;
}
.border-green {
  border-color: #8ED62B !important;
}
.border-red {
  border-color: #E4453B !important;
}
.border-orange {
  border-color: #F7BC33 !important;
}
.border-left {
  border-left: 1px solid #ddd!important;
}
.border-right {
  border-right: 1px solid #ddd!important;
}
.border-thick {
  border-width: 8px!important;
}
.background-gray {
  background-color: #D3D3D3;
}
.overlay-wrapper {
  position: relative;
}
.overlay-wrapper > .overlay {
  background-color: #000;
  bottom: 0;
  color: #fff;
  opacity: .5;
  padding: 10px 15px;
  position: absolute;
}
@media (max-width: 767px) {
  .text-center-xs {
    text-align: center;
  }
}
.panel {
  border-radius: 0;
}
.panel.panel-default .panel-heading {
  background-color: #fff;
  border-radius: 0;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
}
.panel.panel-default .panel-heading.panel-heading-inverse {
  background-color: #07256C;
  color: #fff;
}
.panel.panel-default .panel-body {
  border-radius: 0;
}
.panel.panel-default .panel-body > [type="pills"] {
  margin-left: -15px;
  margin-right: -15px;
  margin-top: -16px;
}
.panel.panel-default .panel-footer {
  background-color: #fff;
  border-radius: 0;
  border-top: none;
}
.btn {
  text-transform: uppercase;
}
.btn.btn-default {
  background-color: #646464;
  border-color: #646464;
  color: #fff;
}
.btn.btn-default-outline {
  background-color: #fff;
  border-color: #646464;
  color: #646464;
}
.btn.btn-primary {
  background-color: #07256C;
  border-color: #07256C;
  color: #fff;
}
.btn.btn-primary-outline {
  background-color: #fff;
  border-color: #07256C;
  color: #07256C;
}
.btn.btn-secondary {
  background-color: #fff;
  border-color: #fff;
  color: #000;
}
.nav-pills {
  background-color: #DFDFDF;
  text-align: center;
}
.nav-pills > li {
  display: inline-block;
  float: none;
  text-transform: uppercase;
}
.nav-pills > li.active > a,
.nav-pills > li.active > a:focus,
.nav-pills > li.active > a:hover {
  background-color: #07256C;
}
.nav-pills > li > a {
  background-color: transparent;
  border-radius: 0;
  color: #07256C;
}
.thumbnail {
  border: none;
  clear: both;
}
@media (max-width: 767px) {
  .thumbnail > img,
  .thumbnail > .video,
  .thumbnail > .overlay-wrapper {
    float: left;
    width: 50%;
  }
  .thumbnail > .caption {
    float: right;
    width: 50%;
  }
}
.list-inline > li > h3 {
  display: inline-block;
  margin: 0;
  padding-top: 6px;
  padding-bottom: 5px;
  vertical-align: middle;
}
form {
  margin-bottom: 20px;
  margin-top: 20px;
}
.list-group.list-group-inline.list-group-inverse {
  background-color: #07256C;
}
.list-group.list-group-inline.list-group-inverse li.list-group-item {
  background-color: #07256C;
  color: #fff;
}
.list-group.list-group-inline li.list-group-item {
  border: none;
  display: inline-block;
}
img.img-border {
  border: 3px solid black;
}
img.img-small {
  height: 56px;
  width: 56px;
}
img.img-disabled {
  opacity: 0.4;
}
